.box{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap
}
.material-icons{
  font-size: 2.4em;
  cursor: pointer;
}
.card{
  width: 300px;
  height:auto;
}
@media only screen and (max-width: 600px) {
  .card {
    width:400px;
    margin-left: 8px !important;
  }
}
.card .card-image .card-title{
  color: black;
  font-weight: 400;
  position: relative;
  
}
.cart{
  margin-top:50px;
}
.collection .collection-item{
  display: flex;
}
.collection-item.avatar .title {
  font-size: 20px;
  font-weight: 500;
  color: #ee6e73;
  display: block;
  margin-bottom: 20px;
  
}
.collection .collection-item img{
  width: 150px;
  margin-right: 25px;
}
.item-desc{
  align-self:center;
}
.btn-right{
  align-items:right;

  /* display: flex; */
  /* justify-content: end; */
  margin-left: 40px;
  
}
input[type=text],input[type=password] {
  
  border: 2px solid gray !important;
  display: flex;
  
}
.d-flex{
  justify-content: center;
  align-items: center;
  display: flex;
}
.mt-10{
  margin-top: 10px !important ;
}
nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
}
.p-10{
  padding-top: 50px;
}
